@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@500&family=Yellowtail&family=IBM+Plex+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.appWrapper {
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow-x: hidden;
}

.routesWrapper {
  flex-grow: 1;
}
.navbar {
    background-color: #676767 !important;
    padding-bottom: 20px !important;
}

nav a {
    color: #FFFFFF !important;
}

.nav-item {
    border: 2px solid transparent !important;
}

.nav-item:hover {
    text-decoration: none !important;
    border-bottom: 2px solid #FFFFFF !important;
    transition: 0.5s ease !important;
    transform: scale(1.1) !important;
}

nav {
    font-family: 'Orbitron', sans-serif;
}
footer {
    width: 100%;
    left: 0;
    bottom: 0;
    font-family: 'Yellowtail', cursive;
    font-size: 25px;
    color: #FFFFFF !important;
    background-color: #676767 !important;
}

small {
    padding-left: 25px;
}

.copyright {
    left: 0 !important;
    text-align: left !important;
    padding-top: 8px;
    padding-left: -15px;
}

/*FOOTER SOCİAL ICONS*/
ul.social-network {
    list-style: none;
    display: inline;
    padding-right: 25px;
}
  
ul.social-network li {
    display: inline;
    margin: 0 .1em;
}

/*social icons hover actions*/

.social-network a.icoTwitter:hover {
    background-color: #1DA1F2;;
}

.social-network a.icoLinkedin:hover {
    background-color: #2867B2;
}

.social-network a.icoMail:hover {
    background-color: #BBB6B6
}

.social-network a.icoGitHub:hover {
    background-color:  #BBB6B6;
}

.social-network a.icoGitHub:hover i,
.social-network a.icoTwitter:hover i,
.social-network a.icoLinkedin:hover i,
.social-network a.icoMail:hover i   {
    color: #fff;
}

.social-circle li a {
    display: inline-block;
    position: relative;
    margin: 0 auto 0 auto;
    border-radius: 50%;
    text-align: center;
    width: 30px;
    height: 30px;
    font-size: 15px;
}

.social-circle li i {
    margin: 0;
    line-height: 30px;
    text-align: center;
}

.social-circle li a:hover i,
.triggeredHover {
    -moz-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -ms--transform: rotate(360deg);
    transform: rotate(360deg);
    transition: all 0.2s;
}

.social-circle i {
    color: #595959;
    transition: all 0.8s;
}

.social-network a {
    background-color: #F1F1F1;
}

h1 {
    color: #B2BEB5;
}
.jumbotron {
    margin: 15px;
    margin-top: 25px;
    box-shadow: 10px 10px #808080;
    border: 1px solid #676767;
}
body {
    background-color: #D4D9DD;
}

.myCarousel {
    height: 650px;
    width: 800px;
    object-fit: scale-down;
}

h3 {
    text-align: center;
    padding-top: 30px;
    padding-bottom: 15px;
    color: #B2BEB5;
}

.carousel-item {
    height: 500px;
    width: 100%;
    object-fit: scale-down;
    border: 2px solid #676767;
    padding: 5px;
}

.carousel-inner img {
    width: 100%;
    height: 100%;
}

.layer {
    background-color: rgba(140, 140, 140, 1);
    margin-bottom: 45px;
    padding-top: 10px;
    padding-bottom: 10px;
}

.carousel-control-prev-icon,
.carousel-control-next-icon {
    -webkit-filter: invert(60%);
            filter: invert(60%);
    height: 40px;
    width: 40px;
    border-radius: 50%;
}

.card img {
    border: 2px solid #676767;
    padding: 5px;
}
h5 {
    color: #B2BEB5 !important;
    font-size: x-large;
}

form {
    background-color: #f5f5f5 !important;
    padding: 10px;
    border: 1px solid black;
    margin-bottom: 15px;
    margin-left: 5px;
    margin-right: 5px;
}
